import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Mascota } from "../../clases/mascota";

@Component({
  selector: 'app-carga-mascota',
  templateUrl: './carga-mascota.component.html',
  styleUrls: ['./carga-mascota.component.css']
})
export class CargaMascotaComponent implements OnInit {

  @Output() turno =  new EventEmitter<any>();
  @Input () nombre : string;
  @Input () imagen :string;
  @Input() tipo : string;
  //otraImagen = new FileReader();

  //public lista : Array<Mascota>  = [];
  
  constructor() { }

  ngOnInit(): void {
    
  }

  public enviarMascota(event)
  {
    return this.turno.emit(new Mascota(this.nombre,this.tipo,"../../../assets/"+this.imagen.slice(12)));
  }

}
